.font-option-icon {
    background-color: #eef0fb;
    color: #91939f;
    border-radius: 50%;
    padding: 20px;
    margin-inline-end: 10px;
    // font-size: 20px;
}

.selected {
    background-color: #171a37;
    color: #d9dbe5;
}

.font-option-icon:last-child {
    margin-inline-end: unset;
}

@include for-mobile-wide-layout {
    .font-option-icon {
        padding: 10px;
        margin-inline-end: 20px;
    }
}
